<template>
	<div class="sidebar" @mouseover="focusSidebar" @mouseleave="unfocusSidebar" v-if="isSidebarVisible">
		<div class="sidebar-top">
			<div class="sidebar-top__header">
				<div class="button button--profile" title="Настройки профиля" @click="openProfile" v-if="showButtonProfile">
					<icon icon="user" />
				</div>

				<div class="button button--back" title="Назад" @click="back" v-if="showButtonBack">
					<icon icon="long-arrow-alt-left" />
				</div>
			</div>

			<router-link class="profile-control" to="/profile"></router-link>

			<div class="button button--menu" title="Дополнительное меню" @click="openMenu">
				<icon icon="bars" />
			</div>

			<div class="button button--add" title="Добавить контент" @click="toggleAddContentMenu">
				<icon :icon="showAddButtons ? 'times' : 'plus'" />
			</div>

			<transition name="fade">
				<div class="add-buttons" v-if="showAddButtons">
					<div class="button button--add-story" title="Добавить историю" @click="addContent(contentTypes.STORY)">
						<icon icon="receipt" />
					</div>

					<div class="button button--add-series" title="Добавить серию" @click="addContent(contentTypes.SERIES)">
						<icon icon="feather-alt" />
					</div>

					<div class="button button--add-book" title="Добавить книгу" @click="addContent(contentTypes.BOOK)">
						<icon icon="journal-whills" />
					</div>
				</div>
			</transition>
		</div>

		<div class="sidebar-middle">
			<router-link class="button button-stories" title="Истории" to="/">
				<icon icon="receipt" />
			</router-link>
			<router-link class="button button-series" title="Серии" to="/series">
				<icon icon="feather-alt" />
			</router-link>
			<router-link class="button button-books" title="Книги" to="/books">
				<icon icon="journal-whills" />
			</router-link>
			<router-link class="button button-notions" title="Понятия" to="/glossary">
				<icon icon="cubes" />
			</router-link>
			<router-link class="button button-lore" title="Вселенная" to="/lore">
				<icon icon="dice-d20" />
			</router-link>
		</div>

		<div class="sidebar-bottom">
			<div class="button button-reload" title="Проверить наличие нового контента" @click="checkNewContent">
				<icon icon="sync-alt" />
			</div>
			<div class="button button-fonts" title="Настройки шрифта" @click="toggleFontSettings">
				<icon icon="eye" />
			</div>
			<div class="button button-corrections" title="Указать на ошибку в тексте" @click="toggleCorrect">
				<icon icon="pencil-ruler" />
			</div>
			<div class="button button-fast-glossary" title="Быстрый словарь" @click="toggleGlossary">
				<icon icon="dragon" />
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "Sidebar",

		data() {
			return {
				showAddButtons: false,
				buttonNodes: [],
				contentTypes: app.contentTypes,
			}
		},
		
		methods: {
			/**
			 */
			back() {
				app.back();
			},
			/**
			 */
			openProfile() {
				this.$router.push('/profile');
			},
			/**
			 */
			checkNewContent() {
				app.dispatch('GET_PRIMARY_DATA');
			},
			/**
			 */
			openMenu() {
				app.$menu.toggleMenu(true);
			},
			/**
			 */
			toggleFontSettings() {
				app.$fontSettings.toggleVisible();
			},
			/**
			 */
			toggleCorrect() {
				app.$correct.toggleVisible();
			},
			/**
			 */
			focusSidebar({ target }) {
				const button = target.closest('.button');

				if (null !== button) {
					const title = button.getAttribute('title');
					app.commit('SET_SYSTEM_INFO', title);
				} else {
					app.commit('CLEAR_SYSTEM_INFO');
				}
			},
			/**
			 *
			 */
			unfocusSidebar() {
				app.commit('CLEAR_SYSTEM_INFO');
			},
			/**
			 */
			addContent(contentType) {
				this.toggleAddContentMenu();

				switch (contentType) {
					case this.contentTypes.STORY: this.$router.push('/story/form'); break;
					case this.contentTypes.SERIES: this.$router.push('/series/form'); break;
					case this.contentTypes.BOOK: this.$router.push('/book/form'); break;
				}
			},
			/**
			 */
			toggleAddContentMenu() {
				this.showAddButtons = !this.showAddButtons;
			},
			/**
			 */
			toggleGlossary() {
				app.$glossary.toggleVisible();
			},
		},
		
		computed: {
			/**
			 */
			isSidebarVisible() {
				return 'home' !== this.$route.name && 'login' !== this.$route.name;
			},
			/**
			 */
			showButtonBack() {
				return this.$route.path !== '/';
			},
			/**
			 */
			showButtonProfile() {
				return this.$route.path !== '/profile';
			},
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.sidebar {
		display: flex;
		flex-direction: column;
		align-items: center;

		.button {
			border-radius: 50%;
			text-align: center;
			cursor: pointer;
		}

		.sidebar-top, .sidebar-middle, .sidebar-bottom {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}

		.sidebar-top {
			position: relative;

			.profile-control {
				width: 50px;
				height: 50px;
				background-color: @color-dark;
				border-radius: 50%;
				margin-top: 10px;
				cursor: pointer;
			}

			&__header {
				display: flex;
				flex-direction: row-reverse;
				justify-content: space-between;
				width: 60px;
			}

			.button {
				@stbSize: 28px;
				width: @stbSize;
				height: @stbSize;
				line-height: @stbSize;
				border: 1px solid @color-beige;
				color: @color-beige;
				font-size: 12px;
				margin-top: 13px;

				@miniButtonSize: 18px;
				&.button--back, &.button--profile {
					width: @miniButtonSize;
					height: @miniButtonSize;
					line-height: @miniButtonSize;
					font-size: 12px;
					align-self: end;
				}

				&.button--profile {
					font-size: 8px;
				}
			}

			.add-buttons {
				position: absolute;
				top: 100%;
				padding: 10px;
				background-color: @color-pretty-dark;

				.button {
					@addButtonSize: 20px;
					width: @addButtonSize;
					height: @addButtonSize;
					line-height: @addButtonSize;
				}
			}
		}

		.sidebar-middle {
			margin: auto 0;

			@smbSize: 48px;
			.button {
				font-size: 21px;
				width: @smbSize;
				height: @smbSize;
				line-height: @smbSize + 2;
				color: white;
				margin-bottom: 15px;
				background-color: @color-dark;
				box-shadow: inset 0 10px 5px black;
				overflow: hidden;

				&:hover {
					color: @color-beige;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.sidebar-bottom {
			display: grid;
			grid-template-columns: 40px 27px;

			@sbbSize: 28px;
			.button {
				width: @sbbSize;
				height: @sbbSize;
				line-height: @sbbSize;
				color: white;
				font-size: 12px;
				background-color: @color-dark;
				margin-bottom: 12px;
				text-align: center;
			}
		}

		.fade-enter-active, .fade-leave-active {
			transition: opacity .1s;
		}
		.fade-enter {
			opacity: 0;
		}
	}
</style>


